import React, { useEffect, useState } from 'react';
import Tabletop from 'tabletop';
import { Container, Row, Col, } from 'react-bootstrap';
import link from '../components/images/link.png';
import smallpathDown from '../components/images/SmallPathDown.png';
import smallpathUp from '../components/images/smallPathUp.png';

const Tester = () => {

    const [data, setData] = useState([]);
    const [error, setError] = useState('');
    const [show, toggleShow] = useState(true);

    useEffect(() => {
        Tabletop.init({
            key: '1Jb1H_7vERF9atWPhILiXKQ4lZ3CuYwZ6m1lPIlS9qEE',
            simpleSheet: true
        }
        ).then(function (data, tabletop) {
            setData(data);
        })
            .catch((e) => {
                setError('SORRY, WE COULD NOT GET YOUR PODCAST, WE ARE WORKING ON IT.');
            });
    }, []);

    if (error !== '') {
        return <p>ERROR: {error}</p>;
    }

    else {
        return (
            <React.Fragment>
                <div>
                    {data.map((obj) => (
                        <div key={obj.id}>
                            <Row className="testimonyCards cards mt-5 ml-1 mb-4">
                                <Col xs={2} sm={2} md={2} lg={2}>
                                    <img src={obj.PodcastArt} alt="Podcast Art" className="addStyle ml-1 mr-2" />
                                </Col>
                                <Col xs={10} sm={10} md={10} lg={10}>
                                    <h5 className="header">
                                        <b ><a href={obj.Podcastwebsite}> {obj.PodcastTitle}</a> </b>
                                        <img src={link} alt="link" className="ml-3 imgStyle" />
                                    </h5>
                                    <p className="pb-4">
                                        {obj.Description}
                                    </p>
                                </Col>
                                <hr/>
                                <Col xs={10} sm={10} md={10} lg={10} className="pt-1 pb-2">
                                    <label className="labelStyles">
                                        {obj.PodcastHosted}
                                        </label>
                                </Col>
                                <Col xs={2} sm={2} md={2} lg={2} className="readMore">
                                  <p >
                                      <a onClick={() => toggleShow(!show)} className="moreLess"> {show ? 'More ' : 'Less '} 
                                       info 
                                       
                                        <img src={show ? smallpathUp : smallpathDown} alt="path" className="newPathStyle ml-2"/>
                                        </a>
                                      </p>
                                </Col>
                                {!show && <span className="fullHouse">  
                                      <hr className="mt-1" />
                                <Col xs={12} sm={12} md={12} lg={12} className="ml-3">
                               
                                   <p>
                                   <label className="headerMore pr-2">
                                        <b> Host: </b>
                                        </label>
                                        {obj.PodcastHost}
                                       </p>

                                       <p>
                                   <label className="headerMore pr-2">
                                        <b> Website: </b>
                                        </label>
                                        <a href={obj.Podcastwebsite}>
                                        {obj.Podcastwebsite}
                                        </a>
                                       </p>

                                       <p>
                                   <label className="headerMore pr-2">
                                        <b> Podcast Category:  </b>
                                        </label>
                                        {obj.DesignAspect}
                                       </p>
                                </Col></span>}
                            

                            </Row>
                        </div>

                    ))}
                </div>
            </React.Fragment>

        )
    }
}


export default Tester;