import React, { useState, useEffect } from 'react';
import './Nav.css'
import { Navbar, Nav } from 'react-bootstrap';
import styled, { createGlobalStyle, ThemeProvider } from 'styled-components';
import logo from '../images/Podcastslogo.png';
import {
  BrowserRouter as Router,
  Switch,
  Route,
  Link
} from "react-router-dom";
import About from '../../containers/About/About';
import Home from '../../containers/Home/Home';


const GlobalStyles = createGlobalStyle`
  *,
  *::after,
  *::before {
    box-sizing: border-box;
  }

  body {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    height: auto;
    background: ${({ theme }) => theme.body};
    color: ${({ theme }) => theme.text};
    padding: 0;
    margin: 0;
    font-family: Helvetica;
    transition: all 0.25s linear;
  }

  body .cards{
    background: ${({ theme }) => theme.cards};
  }

  .pathStyle{
    color: ${({ theme }) => theme.text};
  }
  
  footer {
    position: absolute;
    bottom: 5%;
    left: 50%;
    transform: translateX(-50%);
  }

  small {
    display: block;
  }

  a {
    color: ${({ theme }) => theme.text};
  }

  .itemNav{
    height: 57px;
font-family: 'Helvetica' sans serif;
text-align: center;
display: flex;
justify-content: center;
font-size: 16px;
line-height: 40px;
font-weight:bold;
  }

  

  hr{
    width: 100%;
    color: ${({ theme }) => theme.body};
    margin-top: -.1rem;
 }

 .labelStyles{
  background: ${({ theme }) => theme.label};
  border-radius: 3.2px;
  padding: 2px 10px;
  margin-right: 1rem;
  font-size: 12px;

 }
 
 .accordion-title{
  color: ${({ theme }) => theme.accordionHead};
 }

  .borderright{
    border-right: 1px dashed ${({ theme }) => theme.dashed};
    padding-right: 2rem;

  }

  .borderLeft{
    border-left: 1px dashed ${({ theme }) => theme.dashed};
  }

  .leftarrowed{
    border-left: 1px dashed ${({ theme }) => theme.dashed};
    border-right: 1px dashed ${({ theme }) => theme.dashed};
  }

  .navBackground{
    border-bottom: 1px dashed ${({ theme }) => theme.dashed};
    height: auto
  }

  .header{
    color: ${({ theme }) => theme.head};
  }

  .headerMore{
    color: ${({ theme }) => theme.head};
    display: contents
  }

  .setter{
    width: 100%;
    height: 128px;
    left: 771px;
    top: 155px;
    background: ${({ theme }) => theme.set};
  }

  .pin {
    background-color: ${({ theme }) => theme.pin};
    border-radius: 50%;
    height: 16px;
    left: 4px;
    position: absolute;
    top: 1px;
    width: 16px;
    transition: left ease .3s;
  }

  button{
    background: ${({ theme }) => theme.pinButton};
    border: 0.9px solid ${({ theme }) => theme.pinBorder};
    display: block;
    font-family: Helvetica;
    font-size: 8px;
    line-height: 12px;
    color: ${({ theme }) => theme.toggleText};
    padding: ${({ theme }) => theme.toggleID} 
  }


 .logoText{
  font-family: 'Azedo', sans serif;
  font-style: normal;
  font-weight: bold;
  font-size: 20px;
  line-height: 20px;
  color: ${({ theme }) => theme.textFair};
 }

 .navbar-toggler{
 padding: 0 !important;
 font-size: 1rem !important;
 line-height: 1;
 background: ${({ theme }) => theme.textFair};
 border: none !important;
 border-radius: .25rem;
 margin-right: .5rem
 }

 .pathStyle{
  filter: ${({ theme }) => theme.filter};
  height: 100%;
  margin-top: .5rem;
 }

 .newPathStyle{
  filter: ${({ theme }) => theme.filter};
  margin-top: -.2rem;
 }

 .oval{
  filter: ${({ theme }) => theme.filter};
 }

 .vl {
  border-left: 1px dashed ${({ theme }) => theme.dashed};
  height: 100vh;
}

.checkboxStyle{
  font-family: Helvetica;
font-style: normal;
font-weight: normal;
font-size: 14.4px;
line-height: 17px;
color: ${({ theme }) => theme.checkbox} ;
}

.checkLabel{
background: ${({ theme }) => theme.checkboxBox} !important;
border: 1px solid ${({ theme }) => theme.checkboxBorder} !important;
border-radius: 2px;
}

.para{
  font-style: normal;
font-weight: normal;
font-size: 16px;
line-height: 18px;
color: ${({ theme }) => theme.aboutParagraph}
}

`;

const lighttheme = {
  body: '#F3F3F7',
  text: '#363537',
  toggleBorder: '#fff',
  gradient: 'linear-gradient(#39598A, #79D7ED)',
  cards: '#ffffff',
  dashed: '#979797',
  label: '#EFEFEF',
  head: '#000000',
  set: '#EAEAF0',
  textFair: "#000000",
  pin: '#D8D8D8',
  pinButton: "#ffffff",
  pinBorder: '#FFFFFF',
  filter: '#000000',
  checkbox: '#7A7A7A',
  checkboxBox: '#FFFFFF',
  checkboxBorder: ' #CACACA',
  aboutParagraph: '#3F3F3F',
  accordionHead: '#3F3F3F',
  toggleText: '#585858',
  toggleID: '0  1rem 0 0'
}

const darkTheme = {
  body: '#181818',
  text: '#787878',
  toggleBorder: '#6B8096',
  gradient: 'linear-gradient(#091236, #1E215D)',
  cards: '#262626',
  dashed: '#4b4b4b',
  label: '#353535',
  head: '#BCBCBC',
  set: '#292929',
  textFair: '#ffffff',
  pin: '#ffffff',
  pinButton: '#000000',
  pinBorder: '#404040',
  filter: 'brightness(0) invert(1)',
  checkbox: '#777777',
  checkboxBox: '#222222',
  checkboxBorder: '#4E4E4E',
  aboutParagraph: '#A1A1A1',
  accordionHead: '#B8B8B8',
  toggleText: '#C2C2C2',
  toggleID: '0 0 0 1.2rem'
}

function ToggleButton({ theme, toggleTheme }) {
  const [on, setOnState] = React.useState(false);
  const toggled = () => setOnState(o => !o);
  const isLight = theme === 'dark';



  return (
    <span onClick={toggled}>
      <button className={on ? 'on' : 'off'} on={on} lightTheme={isLight} onClick={toggleTheme}>
        {on ? 'Light' : 'Dark'}
        <span className="pin" data-on="ON" data-off="OFF"> </span>
      </button>
    </span>
  );
};

const useDarkMode = () => {
  const [theme, setTheme] = useState('light');

  const toggleTheme = () => {
    if (theme === 'light') {
      setTheme('dark')
      window.localStorage.setItem('theme', 'dark');
    } else {
      setTheme('light')
      window.localStorage.setItem('theme', 'light');
    }
  };

  useEffect(() => {
    const localTheme = window.localStorage.getItem('theme');

    if (localTheme) {
      setTheme(localTheme);
    } else {
      window.localStorage.setItem('theme', 'light');
    }
  })

  return [theme, toggleTheme]
};

function Navigation() {

  const [theme, toggleTheme] = useDarkMode();
  const themeMode = theme === 'light' ? lighttheme : darkTheme;
  const addPodcastsURL = 'https://forms.gle/ySmobbQvX8hCUgEe9';

  return (
    <div>
      <Router>
        <Navbar collapseOnSelect expand="lg" className="navBackground navbared p-0">
          <Navbar.Brand href="#home" className="logoGroup">
            <h4 className="logoText">
              DESIGN PODCASTS
            </h4>

            <ThemeProvider theme={themeMode} >
              <GlobalStyles />
              <ToggleButton theme={theme} toggleTheme={toggleTheme} />
            </ThemeProvider>
          </Navbar.Brand>
          <Navbar.Toggle aria-controls="responsive-navbar-nav" className="toggle" />
          <Navbar.Collapse id="responsive-navbar-nav">
            <Nav className="m-auto">

            </Nav>
            <Nav className="mr-5 navitems">
              <Nav.Link className="itemNav ml-5 pl-5 ">
                <Link to="/" className="a">
                  Home
              </Link>
              </Nav.Link>
              <Nav.Link className="leftarrowed itemNav ml-5 mr-5 pl-5 pr-5">
                <Link to="/about" className="a">
                  About
              </Link>
              </Nav.Link>
              <Nav.Link className="itemNav" href={addPodcastsURL}>
                <a className="a" href={addPodcastsURL}>
                  Add podcasts
              </a>
              </Nav.Link>
            </Nav>
          </Navbar.Collapse>

        </Navbar>


        <Switch>
          <Route exact path="/">
            <Home />
          </Route>
          <Route path="/about">
            <About />
          </Route>
        </Switch>

      </Router>

    </div>
  )
}

export default Navigation;