import React from 'react';
import './Checkbox.css';
import Tabletop from 'tabletop';

class CheckboxTwo extends React.Component{
    constructor(props){
      super(props);
      this.state = {
        checkbox: [], // state defination for the component
        data: []
      }
    }

    
    componentDidMount() {
      Tabletop.init({
        key: '1Jb1H_7vERF9atWPhILiXKQ4lZ3CuYwZ6m1lPIlS9qEE',
        callback: googleData => {
          this.setState({
            data: googleData
          })
        },
        simpleSheet: true
      })
    }

    handleCheckbox(event){
      let nCheckbox = this.state.checkbox.slice(); // create a new copy of state value 
      if(this.isValueExist(nCheckbox, event)){ // check if the same value is preexisted in the array
          const index = nCheckbox.indexOf(event.target.value);
          nCheckbox.splice(index, 1); // removing the preexciting value 
      }else{
        nCheckbox.push(event.target.value); // inserting the value of checkbox in the array
      }
      this.setState({
        checkbox: nCheckbox
      });
    }
    
    isValueExist(data, event){
        if(data.length === 0){
          return false;
        }
      
        for(let i = 0; i<= data.length; i++){
          if(event.target.value === data[i]){
            return true;
          }
        }
      return false;
    }
    
    render(){
      
    const { data } = this.state;
     // const {checkbox} = this.state;
      return(
        <div>
           <hr className="accordionHR"/>
          {
           data.map(obj => {
            return (
              <div key={obj.id}>
               <div className="checkbox1">
              
              <input 
                 type='checkbox'
                 className="checkbox checkLabel"
                 onChange={this.handleCheckbox.bind(this)}
              //    checked={checkbox[i] ? true : false}
              />

        <label className="ml-3 checkboxStyle"> {obj.Category}</label>
            </div>
                </div>
            )
          })
         }
        </div>
      )
    }
  }

  export default CheckboxTwo;